.Reset {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: #FFF;
}

.Reset button {
    font-size: 1.5em;
    background-color: #FFF;
    border-radius: 6px;
}


